.menu-bars {
  text-align: center;
  cursor: pointer;
  float: right;
  position: relative;
  z-index: 3;
  margin-top: 18px;
  position: absolute;
  right: 27px;
  top: 86px;
}
.menu-bars:hover .menu-bar {
  animation-play-state: paused;
}

.menu-bar {
  display: block;
  margin: 7px 0px;
  height: 2px;
  width: 40px;
  background-color: #fff;
  border-radius: 2px;
  transform-origin: right;
  animation: sounbars infinite alternate linear;
}

.menu-bar:first-child {
  transform: scaleX(0.2);
  animation-duration: 3s;
}

.menu-bar:nth-child(2) {
  transform: scaleX(0.8);
  animation-duration: 4.5s;
}

.menu-bar:nth-child(3) {
  transform: scaleX(0.5);
  animation-duration: 4s;
}

.menu-bar:nth-child(4) {
  transform: scaleX(0.1);
  animation-duration: 3.5s;
}

@keyframes sounbars {
  to {
    transform: scaleY(1);
  }
}
@keyframes dotmoveDown {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(4px);
  }
  100% {
    transform: translateY(0px);
  }
}
header.active .menu-bars .menuDot {
  animation-play-state: paused;
}

.menu-bars span {
  display: inline-block;
  margin: 0 3px;
  color: #fff;
  vertical-align: middle;
}
.menu-bars .menuDot {
  margin: 0 2px;
  width: 9px;
  height: 9px;
  background: #fff;
  border-radius: 50%;
  animation: dotmoveDown 1.01s ease-in-out infinite;
}
.menu-bars .menuDot:nth-child(1) {
  animation-delay: 0.2s;
}
.menu-bars .menuDot:nth-child(2) {
  animation-delay: 0.5s;
}
.menu-bars .menuDot:nth-child(1) {
  animation-delay: 0.7s;
}

