}
.bannerTitle h1 {
  color: #fff;
  font-size: 78px;
  font-weight: 700;
  margin: 0;
  font-family: "Poppins", "Cairo", "Helvetica", sans-serif;
}
.bannerTitle h1::after {
  content: '';
  display: block;
  position: absolute;
  width: 1px;
  height: 100px;
  background: #fff;
  top: 110%;
  left: 50%;
  max-height: 8vh;
  transform: translateX(-50%);
  pointer-events: none;
}
.bannerTitle span {
  display: block;
  position: relative;
  overflow: hidden;
  opacity: 0;
  transition: all 0.7s ease-in;
}
.bannerTitle span strong {
  position: relative;
  display: inline-block;
  overflow: hidden;
  transform: translateX(-100%);
  transition: all 1.1s ease-in;
}
.bannerTitle span strong::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 0;
  left: 0;
  background: #fff;
  z-index: 2;
  bottom: 2px;
  pointer-events: none;
  transition: all 1.1s ease-in;
  max-width: 90%;
}
.bannerTitle span:nth-child(1) strong {
  transition: all 0.4s ease-in-out;
}
.bannerTitle span:nth-child(1) strong::after {
  animation-delay: 0.5s;
  transition: all 1.8s ease-in;
}
.bannerTitle span:nth-child(2) strong {
  transition: all 0.8s ease-in-out;
}
.bannerTitle span:nth-child(2) strong::after {
  animation-delay: 0.9s;
  transition: all 2.2s ease-in;
}
.bannerTitle span:nth-child(3) strong {
  transition: all 1.2s ease-in-out;
}
.bannerTitle span:nth-child(3) strong::after {
  animation-delay: 1.1s;
  transition: all 3s ease-in;
}
.bannerTitle span:nth-child(4) {
  transition: all 4.1s ease-in;
}
.bannerTitle span:nth-child(4)::after {
  animation-delay: 1.5s;
  transition: all 4s ease-in;
}

.bodyLoaded .bannerTitle span {
  opacity: 1;
}
.bodyLoaded .bannerTitle span::after {
  width: 100%;
  max-width: 100%;
}
.bodyLoaded .bannerTitle span strong {
  transform: translateX(0);
  width: auto;
}
.bodyLoaded .bannerTitle span strong::after {
  width: 100%;
  max-width: 100%;
}
.bodyLoaded .bannerTitle span:nth-child(1):after {
  transition: all 1.8s ease-in;
}
.bodyLoaded .bannerTitle span:nth-child(2):after {
  transition: all 2.2s ease-in;
}
.bodyLoaded .bannerTitle span:nth-child(3):after {
  transition: all 3s ease-in;
}
.bodyLoaded .bannerTitle span:nth-child(4):after {
  transition: all 3s ease-in;
}

